// Fonts



@mixin h2 {
	font-weight: 700;
	font-size: $size-xl;
	line-height: 140%;
	font-family: $font-family-primary;
}	

@mixin h3 {
	font-weight: 600;
	font-size: $size-l;
	line-height: 140%;
	font-family: $font-family-primary;
}

@mixin h5 {
	font-weight: normal;
	font-size: $size-m;
	line-height: 100%;
	font-family: $font-family-primary;
}	

@mixin text {
	font-weight: normal;
	font-size: $size-s;
	line-height: 170%;
	font-family: $font-family-primary;
}	

@mixin small-text {
	font-weight: normal;
	font-size: $size-xs;
	line-height: 170%;
	font-family: $font-family-primary;
}	





// UI

@mixin button-primary ($bg-color: $orange, $text-color: $white, $hover-bg: $light-green ) {
	padding: 20px 34px;
    height: auto;
    width: auto;
	color: $text-color;
	background: $bg-color;
	border: none;
	border-radius: $main-br;
	transition: $transition-main;
	font-size: $size-s;
	
	&:hover {
		background: $hover-bg;
		
	}

	&:active {
		transform: translateY(2px);
	}
}	

