@mixin input (
				$width: 360px, 
				$height: auto, 
				$text-color: $white, 
				$bg-color: $dark-gray,
				$border-color: $white,
				
				) {
	width: $width;
	height: $height;
	padding: 20px 30px 16px 30px;
	border-radius: $main-br;
	font-size: $size-s;
	color: $text-color;
	background: $bg-color;
	border-bottom: 4px solid $border-color;
	// border: none;
	transition: $transition-main;

	&::placeholder {
		color: $text-color; 
	}

	&:hover {
		border-color: $orange;
	}

	&:focus {
		border-color: $light-green;
	}

	&:active {
		border-color: $light-red;
	}
}



// displays
@mixin grid ($columns, $rows, $gap: 0) {
	display: grid;
	display: -ms-grid;
	grid-template-columns: $columns;
	-ms-grid-columns: $columns;
	-ms-grid-rows: $rows;
	grid-rows: $rows;
	gap: $gap;
}

@mixin flex ($align: center, $justify: space-between, $direction: row, $gap: 0) {
	display: flex;
	align-items: $align;
	justify-content: $justify;
	-ms-flex-direction: $direction;
	flex-direction: $direction;
	gap: $gap;
}

